import { BackProvider } from './../../providers/back/back';
import { Component } from '@angular/core';
import { IonicPage, NavController, NavParams, ToastController, LoadingController } from 'ionic-angular';
import _ from 'lodash'

@IonicPage()
@Component({
  selector: 'page-notas-global',
  templateUrl: 'notas-global.html',
})
export class NotasGlobalPage {

  public nTema: any[] = []
  public nSubtema: any[] = []
  public nGaleria: any[] = []
  public nAsesoria: any[] = []
  public nSlide: any[] = []

  constructor(
    public navCtrl: NavController,
    public navParams: NavParams,
    private back: BackProvider,
    private toast: ToastController,
    private load: LoadingController
  ) {
  }

  ionViewDidLoad() {
    
    const l = this.load.create({content: 'Cargando notas...'})
    l.present()

    this.back.getMyNotas(localStorage.getItem('uid')).subscribe(data => {
      this.nTema = _.uniqBy(data.filter(n => n.tema), n => n.tema.id)
      this.nSubtema = _.uniqBy(data.filter(n => n.subtema), n => n.subtema.id)
      this.nGaleria = _.uniqBy(data.filter(n => n.galeria), n => n.galeria.id)
      this.nAsesoria = _.uniqBy(data.filter(n => n.asesoria), n => n.asesoria.id)
      this.nSlide = _.uniqBy(data.filter(n => n.slide), n => n.slide.id)
      console.log(this.nTema);
      
      l.dismiss()
    }, err => {
      l.dismiss()
      this.toast.create({message: 'No se pudieron cargar las notas...', duration: 2000}).present()
      this.navCtrl.setRoot('HomePage')
    })
    
  }

}



